.main__container {
  display: block;
  margin: auto;
  margin-top: 100px;
  justify-content: center;
  width: 1500px;
}

.main__title {
  align-items: center;
  display: flex;
  justify-content: center;

  font-size: 30px;
  font-weight: bold;
  color: #19191A;
  padding-bottom: 20px;
}

.main__charts {
  display: flex;
  justify-content: center;
}

.main__charts-pie {
  position: relative;
  justify-content: center;
  width: 50%;

  /* margin-right: 10px; */
  padding: 20px;

  border: 1px solid var(--c-border-primary);
  border-radius: 8px;
  background-color: #fff;
}

.main__charts-lines {
  position: relative;
  justify-content: center;
  width: 50%;

  /* margin-left: 10px; */
  padding: 20px;

  border: 1px solid var(--c-border-primary);
  border-radius: 8px;
  background-color: #fff;
}

.main__slider {
  display: block;
  position: relative;
  margin-top: 30px
}

.main__supply {
  position: relative;
  display: flex;
  justify-content: center;
}

.main__supply.label {
  opacity: 0.5;
  margin-bottom: 10px;
}

.main__supply-upper {
  color: #191919;
  font-weight: bold;
}

.main__supply-under {
  color: #5551FF;
  font-weight: bold;
}
