.main__box {
  border: 1px solid #5551FF;
  border-radius: 6px;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  background-color: white;

  height: 45px;
  width: 235px;

  font-style: normal;
  font-weight: normal;
  font-size: 13.5px;
  line-height: 20px;
  align-items: center;
  text-align: center;
  color: #191919;
}
