.slider__container {
  position: relative;
  display: flex;
  justify-content: center;
}

.slider__slider {
  margin-left: 30px;
  margin-right: 10px;
  width: 60%;
}

span [class^="MuiSlider-markLabel"] {
  font-weight: bold;
  color: #AEACFF;
  transform: rotate(0deg) translateX(-50%) translateY(-45px);
}

span [class^="MuiSlider-thumb WithStyles(ForwardRef(Slider))-thumb-9 MuiSlider-thumbColorPrimary"] {
  color: #5551FF;
  background-color: #5551FF;
}
